import { NextRequest, NextResponse } from 'next/server'
import { ActionGetResponse, ACTIONS_CORS_HEADERS } from '@solana/actions'


export const OPTIONS = GET;

export async function GET(request: NextRequest) {
  const actionMetaData: ActionGetResponse = {
    icon: 'https://thumbs.dreamstime.com/z/peanut-butter-15388988.jpg?ct=jpeg',
    title: 'Vote for your favorite type of peanut butter',
    description: 'Vote between Cranchy, Smooth peanut butter',
    label: 'Vote',
    links: {
        actions: [
            {
                label: 'Vote Cranchy',
                href: 'https://voting-dapp.vercel.app/api/vote',
                type: 'transaction',
            },
            {
                label: 'Vote Smooth',
                href: 'https://voting-dapp.vercel.app/api/vote',
                type: 'transaction',
            },
        ]
    },
  }

  return NextResponse.json(actionMetaData, { headers: ACTIONS_CORS_HEADERS })
}

export async function POST(request: NextRequest) {
  const body = await request.json()
  console.log('body', body)
  return NextResponse.json({ message: 'Hello World' }, { headers: ACTIONS_CORS_HEADERS })
}
